<template>
<div style="width:100%;height:100%">
    <div class="index-left">
      <menu-common :activeIndex="activeIndex"></menu-common>
    </div>
    <div class="index-right">
      <index-nav-top></index-nav-top>
      <div class="index-right-section">
        <div class="section-content">
          <div class="section-content-top">
            <div class="common-marker fl">
            </div>
            <p class="fl">创建商品</p>
          </div>
        </div>
      </div>
    </div>
</div>
</template>

<script>
import MenuCommon from '../../components/menu'
import IndexNavTop from '../../components/indexNavTop'
export default {
  components: {
    IndexNavTop,
    MenuCommon},
  name: 'Index',
  data () {
    return {
      activeIndex: '1-1'
    }
  }
}
</script>
<style scoped>
.index-left {
  width:240px;height:100%;
  float:left;
}
.index-right{
  height:100%;
  margin-left: 239px;
}
.index-right-section{
  height: 100%;
  position: relative;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 20px;
  background-color: #F2F3F4;
}
.section-content{
  padding: 16px 24px;
  background-color: #ffffff;
  border-radius: 3px;
  min-height: 100%;
}
</style>



